import * as actionTypes from "./actions";

export const randomNumberGenerateOnStart = (topRow, bottomRow) => {
  return {
    type: actionTypes.GENERATEONSTART,
    topRow: topRow,
    bottomRow: bottomRow,
  };
};
export const generatedOnStart = (topRow, bottomRow) => {
  return {
    type: actionTypes.GENERATEDONSTART,
    topRow: topRow,
    bottomRow: bottomRow,
  };
};

export const startMultiplication = (topRow, bottomRow) => {
  return {
    type: actionTypes.START,
    topRow: topRow,
    bottomRow: bottomRow,
  };
};
export const endMultiplication = () => {
  return {
    type: actionTypes.END,
  };
};

export const goPrev = (position, upperDigit, lowerDigit, resultDigits) => {
  return {
    type: actionTypes.PREV,
    upperDigit: upperDigit,
    lowerDigit: lowerDigit,
    position: position,
    resultDigits: resultDigits,
  };
};
export const goNext = (
  position,
  carryNumbers,
  upperDigit,
  lowerDigit,
  resultDigits
) => {
  return {
    type: actionTypes.NEXT,
    position: position,
    c: carryNumbers,
    upperDigit: upperDigit,
    lowerDigit: lowerDigit,
    resultDigits: resultDigits,
  };
};

export const keypress = (key, changeFn) => {
  return {
    type: actionTypes.KEYPRESS,
    key: key,
    position: changeFn,
  };
};
export const multiply = () => {
  return {
    type: actionTypes.KEYPRESSMULTI,
  };
};
export const deleteDigit = (position) => {
  return {
    type: actionTypes.DELETEDIGIT,
    position: position,
  };
};

export const keypadToggle = () => {
  return {
    type: actionTypes.KEYPADTOGGLE,
  };
};
export const keypadHide = () => {
  return {
    type: actionTypes.KEYPADHIDE,
  };
};
export const keypadShow = () => {
  return {
    type: actionTypes.KEYPADSHOW,
  };
};
export const sideMenuToggle = () => {
  return {
    type: actionTypes.SIDEMENUTOGGLE,
  };
};
export const sideMenuClose = () => {
  return {
    type: actionTypes.SIDEMENUCLOSE,
  };
};
export const pHeadersToggle = () => {
  return {
    type: actionTypes.PHEADERSTOGGLE,
  };
};
export const gridToggle = () => {
  return {
    type: actionTypes.GRIDTOGGLE,
  };
};
export const carryToggle = () => {
  return {
    type: actionTypes.CARRYTOGGLE,
  };
};
