import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import Header from "../../components/BoardHeaderRow/Row";
import Carry from "../../components/BoardCarryRow/Row";
import Result from "../../components/BoardResultsRow/Row";
import Span from "../../components/BoardCarryRowSpan/Span";
import classNames from "./Board.module.css";

const Board = (props) => {
  let rows;

  switch (props.resultRow) {
    case 1:
      rows = (
        <>
          <Result n={props.results[0]} color="r1" blank />
        </>
      );
      break;
    case 2:
      rows = (
        <>
          <Result n={props.results[0]} color="r1" blank />
          <Result n={props.results[1]} color="r2" blankH zeroO />
          <Result n={props.finallResults} color="r4" />
        </>
      );
      break;
    case 3:
      rows = (
        <>
          <Result n={props.results[0]} color="r1" blank />
          <Result n={props.results[1]} color="r2" blankH zeroO />
          <Result n={props.results[2]} color="r3" zero />
          <Result n={props.finallResults} color="r4" />
        </>
      );
      break;

    default:
      rows = null;
      break;
  }

  const focusColor = (c1) => {
    const yellow = { backgroundColor: "yellow" };
    let color;
    if (c1) {
      color = yellow;
    } else {
      color = null;
    }
    return color;
  };

  let topRowClasses;
  if (props.grid) {
    topRowClasses = `${classNames.digits} ${classNames.brc}`;
  } else {
    topRowClasses = `${classNames.digits}`;
  }

  let bottomRowClasses;
  if (props.grid) {
    bottomRowClasses = `${classNames.digits} ${classNames.brdr} ${classNames.brd}`;
  } else {
    bottomRowClasses = `${classNames.digits} ${classNames.brdr}`;
  }

  let headerForMobile;
  if (window.innerWidth > 924) {
    headerForMobile = props.pheader ? <Header /> : null;
  } else {
    headerForMobile = props.pheaderMobile ? <Header /> : null;
  }

  const [focusingInput, setFocusingInput] = useState({ t: {}, b: {} });
  const [carry, setCarry] = useState({});
  useEffect(() => {
    const boardContainer = document.getElementById("boardContainer");
    boardContainer.scroll({
      top: boardContainer.scrollHeight,
      behavior: "smooth",
    });
    if (props.zeroFC) {
      setFocusingInput({
        t: {
          [props.t]: true,
        },
        b: {
          [props.b]: true,
        },
      });
    } else {
      setFocusingInput({ t: {}, b: {} });
    }
    setCarry({});
    if (props.zeroFC) {
      if (props.c !== null) {
        setCarry({ [props.c]: props.carries[props.c][props.p] });
      }
    } else {
      setCarry({});
    }
  }, [
    props.resultRow,
    props.t,
    props.b,
    props.zeroFC,
    props.c,
    props.p,
    props.carries,
  ]);

  return (
    <div className={classNames.container} id="boardContainer">
      <div className={classNames.inner}>
        {headerForMobile}

        {props.carry ? <Carry color="c3" /> : null}

        <div className={classNames.row}>
          <div className={topRowClasses}>
            <h1>{null}</h1>
          </div>
          <div className={topRowClasses}>
            <h1>{null}</h1>
          </div>
          <div className={topRowClasses}>
            {!props.carry ? <Span n={carry[2]} /> : null}
            <h1>{null}</h1>
          </div>
          <div className={topRowClasses} style={focusColor(focusingInput.t[0])}>
            {!props.carry ? <Span n={carry[1]} /> : null}
            <h1>{props.tR[0]}</h1>
          </div>
          <div className={topRowClasses} style={focusColor(focusingInput.t[1])}>
            {!props.carry ? <Span n={carry[0]} /> : null}
            <h1>{props.tR[1]}</h1>
          </div>
          <div className={topRowClasses} style={focusColor(focusingInput.t[2])}>
            <h1>{props.tR[2]}</h1>
          </div>
        </div>

        <div className={classNames.row}>
          <div className={`${bottomRowClasses}`}>
            <h1 className={classNames.cross}>X</h1>
          </div>
          <div className={`${bottomRowClasses}`}>
            <h1>{null}</h1>
          </div>
          <div className={`${bottomRowClasses}`}>
            <h1>{null}</h1>
          </div>
          <div
            className={`${bottomRowClasses} ${classNames.bI1}`}
            style={focusColor(focusingInput.b[0])}
          >
            <h1>{props.bR[0]}</h1>
          </div>
          <div
            className={`${bottomRowClasses} ${classNames.bI2}`}
            style={focusColor(focusingInput.b[1])}
          >
            <h1>{props.bR[1]}</h1>
          </div>
          <div
            className={`${bottomRowClasses} ${classNames.bI3}`}
            style={focusColor(focusingInput.b[2])}
          >
            <h1>{props.bR[2]}</h1>
          </div>
        </div>

        {rows}
      </div>
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    tR: state.number.topRow,
    bR: state.number.bottomRow,
    results: state.result.products,
    t: state.result.tF,
    b: state.result.bF,
    zeroFC: state.result.zeroFC,
    finallResults: state.result.finallResults,
    resultRow: state.number.resutltsRow,
    pheader: state.menu.pheader,
    pheaderMobile: state.menu.pheaderMobile,
    grid: state.menu.grid,
    carry: state.menu.carry,
    c: state.result.c,
    p: state.result.p,
    carries: state.multi.answer.carries,
  };
};

export default connect(mapStatetoProps)(Board);
