import React from "react";
import { connect } from "react-redux";
import classNames from "../../containers/NumberBoard/Board.module.css";

const Row = (props) => {
  const digit = props.n ? props.n : [];

  let classes;
  switch (props.color) {
    case "r1":
      if (props.grid) {
        classes = `${classNames.results} ${classNames.r1} ${classNames.brf}`;
      } else {
        classes = `${classNames.results} ${classNames.r1}`;
      }
      break;
    case "r2":
      if (props.grid) {
        classes = `${classNames.results} ${classNames.r2} ${classNames.brs}`;
      } else {
        classes = `${classNames.results} ${classNames.r2}`;
      }
      break;
    case "r3":
      if (props.grid) {
        classes = `${classNames.results} ${classNames.r3} ${classNames.brt}`;
      } else {
        classes = `${classNames.results} ${classNames.r3}`;
      }
      break;
    case "r4":
      if (props.grid) {
        classes = `${classNames.results} ${classNames.r4} ${classNames.brl} ${classNames.btlr}`;
      } else {
        classes = `${classNames.results} ${classNames.r4} ${classNames.btlr}`;
      }
      break;

    default:
      break;
  }
  let zeroColor = "#D9D9D9";
  if (window.innerHeight < 420) {
    zeroColor = "black";
  }
  return (
    // #ffffc0
    <div className={classNames.row}>
      <div
        className={classes}
        style={
          props.blank || props.blankH
            ? { backgroundColor: "transparent" }
            : null
        }
      >
        <h1>{digit[5]}</h1>
      </div>
      <div
        className={classes}
        style={props.blank ? { backgroundColor: "transparent" } : null}
      >
        <h1>{digit[4]}</h1>
      </div>
      <div className={classes}>
        <h1>{digit[3]}</h1>
      </div>
      <div className={classes}>
        <h1>{digit[2]}</h1>
      </div>
      <div
        className={classes}
        style={
          props.zero
            ? { backgroundColor: "transparent", color: zeroColor }
            : null
        }
      >
        <h1>{digit[1]}</h1>
      </div>
      <div
        className={classes}
        style={
          props.zero || props.zeroO
            ? { backgroundColor: "transparent", color: zeroColor }
            : null
        }
      >
        <h1>{digit[0]}</h1>
      </div>
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    grid: state.menu.grid,
  };
};

export default connect(mapStatetoProps)(Row);
