import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import classNames from "../../containers/NumberBoard/Board.module.css";

const Row = (props) => {
  const [carry, setCarry] = useState({});
  useEffect(() => {
    setCarry({});
    if (props.zeroFC) {
      if (props.c !== null) {
        setCarry({ [props.c]: props.carries[props.c][props.p] });
      }
    } else {
      setCarry({});
    }
  }, [props.c, props.p, props.carries, props.zeroFC]);
  let classes;
  switch (props.color) {
    case "c1":
      if (props.grid) {
        classes = `${classNames.carry} ${classNames.c1} ${classNames.brc}`;
      } else {
        classes = `${classNames.carry} ${classNames.c1}`;
      }
      break;
    case "c2":
      if (props.grid) {
        classes = `${classNames.carry} ${classNames.c2} ${classNames.brc}`;
      } else {
        classes = `${classNames.carry} ${classNames.c2}`;
      }
      break;
    case "c3":
      if (props.grid) {
        classes = `${classNames.carry} ${classNames.c3} ${classNames.brc}`;
      } else {
        classes = `${classNames.carry} ${classNames.c3}`;
      }
      break;

    default:
      classes = `${classNames.carry} ${classNames.c1} ${classNames.brc}`;
      break;
  }
  return (
    <>
      <div className={`${classNames.row}`}>
        <div className={classes}>
          <h2>{null}</h2>
        </div>
        <div className={classes}>
          <h2>{null}</h2>
        </div>
        <div className={classes}>
          {carry[2] ? (
            <h2 style={{ backgroundColor: "#ffffc0" }}>{carry[2]}</h2>
          ) : null}
        </div>
        <div className={classes}>
          {carry[1] ? (
            <h2 style={{ backgroundColor: "#ffffc0" }}>{carry[1]}</h2>
          ) : null}
        </div>
        <div className={classes}>
          {carry[0] ? (
            <h2 style={{ backgroundColor: "#ffffc0" }}>{carry[0]}</h2>
          ) : null}
        </div>
        <div className={classes}>{null}</div>
      </div>
    </>
  );
};

const mapStatetoProps = (state) => {
  return {
    grid: state.menu.grid,
    carries: state.multi.answer.carries,
    zeroFC: state.result.zeroFC,
    c: state.result.c,
    p: state.result.p,
  };
};
export default connect(mapStatetoProps)(Row);
